.content-main {
    .container-slider {
        #slider {
            .dp-wrap {
                margin: 0 auto;
                position: relative;
                perspective: 1000px;
                height: 100%;
                font-family: "Bebas Neue", sans-serif;
                font-weight: 400;
                #dp-slider 
                    .dp_item {
                        display: flex;
                        align-items: center;
                    
                        border-radius: 10px;
                        overflow: hidden;
                        width: 85%;
                       
                        position: absolute;
                        text-align: center;
                        color: #FFF;
                        border-radius: 10px;
                        transition: transform 1.2s;
                        &:first-child {
                            z-index: 10 !important;
                            transform: rotateY(0deg) translateX(0px) !important;
                        }
                        &[data-position="2"] {
                            z-index: 9;
                            transform: rotateY(0deg) translateX(7%) scale(0.9);
                          }
                        
                        &[data-position="3"] {
                            z-index: 8;
                            transform: rotateY(0deg) translateX(14%) scale(0.8);
                        }
                        
                        &[data-position="4"] {
                            z-index: 7;
                            transform: rotateY(0deg) translateX(21%) scale(0.7);
                        }
                        
                        &[data-position="2"] .dp-content, &[data-position="3"] .dp-content, &[data-position="4"] .dp-content {
                            visibility: hidden;
                        }
                        &:hover:not(:first-child) {
                        cursor: pointer;
                        }
                        .dp-content {
                            padding-left: 100px; 
                            padding-right: 0;
                            display: inline-block;
                            width: 100%;
                            margin-right: -100px;
                            z-index: 99;
                            margin-top: 100px;
                            h2 {
                                color: #393939;
                               font-family: "Bebas Neue", sans-serif;
                                font-size: 80px;
                                max-width: 1100px;
                                letter-spacing: 3px;
                                margin-bottom: 47px;
                                text-transform: uppercase;
                                border-bottom:3px solid #393939;
                                z-index: 999;
                            }
                            p {
              
                                max-width: 490px;
                                margin-top: 15px;
                                font-size: 24px;
                               
                            }
                            .white-text {
                                background: #ffffff;
                                color:#393939;
                                padding: 40px 86px 40px 40px;
                                max-width: 541px;
                                min-height: 200px;
                                border-radius: 10px;
                                font-size: 16px;
                                font-family: "Open Sans", sans-serif;
                                float:right;
                                margin-right: 48px;
                                text-transform: inherit;
                                a {
                                    color:#00B344;
                                    text-decoration: underline;
                                   
                                    font-size: 16px;
                                    font-family: "Open Sans", sans-serif;
                                }
                                span {
                                    font-family: "Bebas Neue", sans-serif;
                                    font-size: 24px
                                }
                            }
                            @keyframes fadeInUp {
                                from {
                                    transform: translate3d(0,40px,0)
                                }
                            
                                to {
                                    transform: translate3d(0,0,0);
                                    opacity: 1
                                }
                            }
                            
                            @-webkit-keyframes fadeInUp {
                                from {
                                    transform: translate3d(0,40px,0)
                                }
                            
                                to {
                                    transform: translate3d(0,0,0);
                                    opacity: 1
                                }
                            }
                            
                            .animated {
                                animation-duration: 1s;
                                animation-fill-mode: both;
                                -webkit-animation-duration: 1s;
                                -webkit-animation-fill-mode: both
                            }
                            
                            .animatedFadeInUp {
                                opacity: 0
                            }
                            
                            .fadeInUp {
                                opacity: 0;
                                animation-name: fadeInUp;
                                -webkit-animation-name: fadeInUp;
                            }
                            
                             
                            .site-btn {
                                margin-top: 150px;
                                font-size: 13px;
                                padding: 19px 40px;
                                color: #fff;
                                font-size: 18px;
                                background: #FA8282;
                                padding: 14px 43px;
                                display: inline-block;
                                border-radius: 2px;
                                position: relative;
                                top: -12px;
                             }

                        }
                        .dp-img {
                            text-align: left;
                            &:before {
                                /* background: -webkit-linear-gradient(-90deg, rgba(255, 255, 255, 0.25), rgba(255, 255, 255, 0));
                                background: -o-linear-gradient(-90deg, rgba(255, 255, 255, 0.25), rgba(255, 255, 255, 0));
                                background: -moz-linear-gradient(-90deg, rgba(255, 255, 255, 0.25), rgba(255, 255, 255, 0));
                                background: linear-gradient(-90deg, rgba(255, 255, 255, 0.75), rgba(255, 255, 255, 0)); */
                                content: "";
                                position: absolute;
                                height: 100%;
                                width: 25%;
                                z-index: 1;
                                top: 0;
                                pointer-events: none;
                                /* background: -webkit-linear-gradient(-90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.75));
                                background: -o-linear-gradient(-90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.75));
                                background: -moz-linear-gradient(-90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.75));
                                background: linear-gradient(-90deg, rgba(255, 255, 255, 0), rgb(255, 255, 255)); */
                            }
                            img {
                                object-fit: cover;
                                object-position: right;
                                height: 610px;
                                border-radius: 10px;
                                border-left: 1px solid #fff;
                                max-width: inherit;
                            }
                           

                        }
                    }
                }
                #dp-next,#dp-prev {
                        position: absolute;
                        top: 50%;
                        right: 5%;
                        transform: rotate(
                        135deg
                        );
                        height: 33px;
                        width: 33px;
                        z-index: 10;
                        color: #00B344;
                        font-size: 37px;
                        cursor: pointer;
                        
                }
                #dp-prev {
                        left: 15px;
                        transform: rotate(180deg);
                        display: none;
                }
                #dp-dots {
                    position: absolute;
                    bottom: 25px;
                    z-index: 12;
                    left: 41%;
                    cursor: default;
                    transform: translate(50%, 50%);
                    li {
                        display: inline-block;
                        width: 13px;
                        height: 13px;
                        margin-left: 52px;
                        background: transparent;
                        border-radius: 50%;
                        border: 1px solid #00B344;
                        &:hover {
                            cursor: pointer;
                            background: #00B344;
                            transition: background .3s;
                        }
                        .active {
                            background: #00B344;
                        }
                    }
                }
                .dp-title {
                    position: absolute;
                    top:0;
                    left:0;
                    transform: translate(10%,30%);
                    z-index: 999;
                    .pre-title {
                        font-family: "Bebas Neue", sans-serif;
                        font-size: 20px;
                        color:#393939;
                        margin-bottom: 0;
                   }
                    h2 {
                
                        color: #393939;
                       font-family: "Bebas Neue", sans-serif;
                        font-size: 80px;
                        width: 725px;
                        letter-spacing: 3px;
                        margin-bottom: 47px;
                        text-transform: uppercase;
                        border-bottom:3px solid #393939;
                        z-index: 999;
                    
                    }
                }
                
            }
            
            
            
            
           
            
            
            
            
        
            
            
            
            
           
            
           
           
           
           
            
            
            
