.full {
    margin: 0;
    padding:0;
}
.port {
    height: 75vh!important;
    margin: 0;
    padding:32px 10px 10px 10px;
    background-image: url('https://dev.wood-tradecomua/libs/img/slide1main.jpg');
    background-position: bottom;
    background-size: cover;
}
.portjs {
    min-height: 50vh!important;
    height: 50vh!important;
}
 
.header-landing
 {    
    
    position: relative;
    
   
    
    max-width: 100%;
   
    .submenu
    {
        padding: 0; 
    }
    .logo-container {
        padding: 0; 
        .logo{
            position: relative;
            z-index: 999;
            
                img {
                    position: relative;
                    top:2px;
                    width: 100%;
                    @media (orientation: landscape)
                    {
                        top:-22px;
                        
                    }

                    @media (orientation: portrait) 
                    {
                        top:-2px;
                    }
                    @media (max-width: 580px) and (min-width: 920px) and (orientation: landscape) 
                    { top:-22px;/*  CSS ... */ }
                    // @media (min-width: $screen-sm-min) {     
                    //     top:-3px;
                    //     width: 80%;
                    // }
                    // @media (min-width: $screen-md-min) {     
                    //     width: 67%;
                    //     top: 0;
                    // }
               
            }
        }
    }
    .navigation-top
    {   
        position: relative;
        z-index: 999;
        a {
            font-family: 'Proxima Nova';  
            color: $nav;
            text-decoration: none;
            font-weight: 700;
            border-right: 1px solid;
            padding: 6px;
            font-size: 16px;
            &:hover
            {
                color: $hoverlink;
            }
        }
        .btn-lg {
            font-size: 1.6em!important;
            position: relative;
            top: 4px;
            padding: 0px;
        }
        .btn-floating
        {
            border: none;
        }
    }
    .navigation-overlay
    {
        a{
            font-family: 'Proxima Nova';  
            font-size: 25px;
            color:$white;
            display: block;
            font-weight: 500;
            text-transform: uppercase;
            padding: 8px;
            text-decoration: none;
            transition: all 0.3s ease 0s;
            @media (min-width: $screen-md-min) { 
                font-size: 2em;
            }
        
        }
    }


    

    .header-image {
        width: 100%;
        height: 50vh;
    }

    .no-g-header {
        padding-left: 0px!important;
        padding-right: 0px!important;
    } 

    
    .heading-contact {
        margin-top: 100px;
        text-align: center;
        font-size: 2rem;
        margin-bottom: 30px;
    }

    
   

    

        
    .langlinks {
        
        .phonebtn {
            background: none;
            border: none;
        }
        a {
            
            &:hover{
            text-decoration: none;
            color:#00abe3;
                cursor: pointer;
            }
        }
        .btn-group{   
            ul{
                li {
                    display: inline-flex;
                }
            }
            @media (max-width: $screen-sm-min) { 
                display: none;
            }
            	
        } 

        .dropdown-menu {
            left: -117px!important;
            min-width: 185px;
            text-align: center;
                li{
                    a{
                        border: none;
                    }
                }
            
        } 
      
    }
        
    .submenu{
        text-align:right;
        @media (min-width: $screen-sm-min) {        
        right:1.2em;
        }
        @media (min-width: $screen-lg-min) {
            right:0;  
        } 
    }
      
    @media (min-width: $screen-sm-min) {        
        
        
    }

    @media (min-width: $screen-md-min) {            
              
        margin: 25px 25px;
    }

    @media (min-width: $screen-lg-min) {
        

        
    }   
    
}


