.breadcrumbs{
    text-align: center;
    z-index: 998; 
    position: relative;
    width: 100%;
    height: 100%;
    display: block;
    position: relative;
    padding: 15px 0px; 
     
      
      .inner-banner-title {
          font-size: 2rem;
          text-transform: uppercase;
          position: absolute;
          transform: translate(-50%,-350%);
          left: 50%;
          top: 50%;
      }

}