/*general css style */
html {
  font-size: 100%; /* This means 16px */
  font-family: 'Proxima Nova'; 
}
body{
  font-family: 'Proxima Nova'!important; 
  line-height: 1;
  color: $black;
 
  background-color:$fd;
  background-image: url("https://dev.wood-tradecomua/libs/img/mainbackground.png");
  overflow-y: hidden;
  background-attachment: fixed;
  text-rendering: optimizeLegibility;
}




article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section, main {
	display: block;
}

ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
b {
    font-weight: bold;
    a{
     font-weight:700!important;
    }
}

::before
{
  box-sizing: border-box;
}



*, *::after, *::before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}


html * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



body, html {
 
  overflow-x: hidden;

}


a {
  color: $nav;
  text-decoration: none;
  font-weight: 700;
}

.overlay{

  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  background-color:$black;
  overflow-x: hidden;
  transition: 0.5s;
 
  @media (min-width: $screen-md-min) and (orientation:landscape) {
    background-color: $blackopas;
    
    
  }
  @media (min-width: 1024px) and (orientation:portrait) {
    background-color: $blackopas;
    
  }
  a{
    font-size: 20px;
    padding: 8px;
    text-decoration: none;
    
    color: $white;
    display: block;
    transition: 0.3s;
    font-weight: 500;
    text-transform: uppercase;      

      &:hover,
      &:focus{
        color: $blueopasit;
      }
  }
  .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
 
     
      
   header {
       width: 100%;
       position: absolute;
   } 

  @media (min-width: $screen-md-min) { 

    a {
      font-size: 36px;       
    }

    .closebtn {
      position: absolute;
      top: 20px;
      right: 45px;
      font-size: 60px;
    }

    .inner-banner-content {  
        left: 21%;
    }    
      
  }  
}



