}
.switch {
    position: fixed;
    display: inline-block;
    width: 60px;
    height: 34px;
    z-index: 999;
    top: 2em;
    /* left: 50%; */
    right: 4%;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  } 
.header
 {    
    
    position: relative;
    padding: 10px 10px;
    text-align: center;
    margin: 25px 0px;
    height: 100vh;
    max-width: 100%;
   
    @media (max-width: $screen-sm-min) {     
        height: 100vh;
       
      }
    .submenu
    {
        padding: 0;
    }
    .logo-container {
        padding: 0;
        .logo{
            position: relative;
            z-index: 999;
            
                img {
                    position: relative;
                    top:2px;
                    
                    @media (orientation: landscape)
                    {
                        top:-22px;   
                    }
                    @media (orientation: portrait)
                    {
                        top:-2px;
                    }
                    @media (max-width: 580px) and (min-width: 920px) and (orientation: landscape) 
                    { top:-22px; }
                }
        }
    }
    .navigation-top
    {   
        position: relative;
        z-index: 999;
        padding: 10px 10px;
        a {
            font-family: 'Proxima Nova';  
            color: $black;
            text-decoration: none;
            font-weight: 700;
            
            padding: 6px;
            font-size: 16px;
            &:hover
            {
                color: $woodgreen;
            }
        }
        .btn-lg {
            font-size: 1.6em!important;
            position: relative;
            top: 4px;
            padding: 0px;
        }
        .btn-floating
        {
            border: none;
        }
    }
    .topnav {
        overflow: hidden;
        
       
        a {
            &:hover {
                background-color: #ddd;
                color: black;
            }
        }
        .icon {
            display: none;
        }
        @media screen and (max-width: 600px) {
	
            a {display: none;}
            
            a.icon {
                float: right;
                display: block;
            }
    
            .responsive {position: relative;
                a {
                float: none;
                display: block;
                text-align: left;
                    }
                .icon {
                position: absolute;
                right: 0;
                top: 0;
                }
                
            }
    }
    }
    
    
    .navigation-overlay
    {
        a{
            font-family: 'Proxima Nova';  
            font-size: 25px;
            color:$white;
            display: block;
            font-weight: 500;
            text-transform: uppercase;
            padding: 8px;
            text-decoration: none;
            transition: all 0.3s ease 0s;
            @media (min-width: $screen-md-min) { 
                font-size: 2em;
            }
        
        }
    }
    .header-image {
        width: 100%;
        height: 50vh;
    }

    .no-g-header {
        padding-left: 0px!important;
        padding-right: 0px!important;
    } 

    
    .heading-contact {
        margin-top: 100px;
        text-align: center;
        font-size: 2rem;
        margin-bottom: 30px;
    }

    .langlinks {
        
        .phonebtn {
            background: none;
            border: none;
        }
        a {
            
            &:hover{
            text-decoration: none;
            color:$blueopasit;
                cursor: pointer;
            }
        }
        .btn-group{   
            ul{
                li {
                    display: inline-flex;
                }
            }
            @media (max-width: $screen-sm-min) { 
                display: none;
            }
            	
        } 

        .dropdown-menu {
            left: -117px!important;
            min-width: 185px;
            text-align: center;
                li{
                    a{
                        border: none;
                    }
                }
            
        } 
      
    }
        
    .submenu{
        text-align:right;
        @media (min-width: $screen-sm-min) {        
        right:1.2em;
        }
        @media (min-width: $screen-lg-min) {
            right:0;  
        } 
    }
    @media (min-width: $screen-md-min) {            
              
        margin: 25px 25px;
    }
}


