.contacts {

    .map{
        width: 100%;
        height: 400px;
    }
    .heading-contact {
        color:$fd;
        h1 {
            font-size: 1.1em;
        }
        a {
            color:$prjctyellow;
        }
    }
    .icon-box-icon{
        display: inline-block;
        vertical-align: top;
        width: 5%;
        color:$hoverlink;
        font-size: 18px;
    }
    .contact-text{
        display: inline-block;
        vertical-align: top;
        width: 84%;
        color:$fd;
        a {
            color:$fd;
        }
    }
    .contact-map{
        margin: auto;
        display: block;
        width: 100%;
        text-align: center;
        line-height: 4em;
        color:$prjctyellow;
    }
}


