.slick-slide {
    border: 1px solid;
    padding: 5px 5px;
    background: none;
    img {
        width: 100%;
    }
}
.gallery-item {
    position: relative;
}
.gallery-item img {
 
    width: 100%;
}
.gallery-item .after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    color: #FFF;
    text-align: center;
    transform: scale(0);
    transition: all .4s;
    
}
.gallery-item:hover .after {
    display: block;
     
    transform: scale(1);
   
    background: rgba(52, 59, 87,.7);

}
.gallery-content {
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
    width: 100%;
    padding: 10px 20px;
    transform: translate3d(-50%,-50%,0);
    font-size: 2rem;
    color: #fff;
    text-decoration: none;
    @media (min-width: $screen-sm-min) {        
        font-size: 3rem;            
                      
    }    
        
    @media (min-width: $screen-lg-min) {            
        font-size: 5rem;
    }
    
    

}
.gallery-content a {
  font-size: 5rem;
    color: $white;
    text-decoration: none;
}


/*---portfolio page--*/
.pt-plus-animated-image-wrapper, .animated-image-parallax {
    position: relative;
    display: block;
    width: 100%;
}
.pt_plus_animated_image.bg-img-animated.creative-animated {
    opacity: 1!important;
}
.pt_plus_animated_image.bg-img-animated {
    opacity: 0;
}
.text-center {
    text-align: center!important;
}
figure, .wp-caption, .wp-caption-overlay .wp-caption {
    border: 0;
    margin: 0;
    padding: 0;
    overflow: hidden;
    position: relative;
    max-width: 100%;
}
.pt_plus_animated_image.text-center .scroll-image-wrap {
    margin: 0 auto;
    text-align: center;
}
.pt-plus-animated-image-wrapper .vc_single_image-wrapper.scroll-image-wrap {
    display: block;
    position: relative;
    margin: 0 auto;
    width: 100%;
    overflow: hidden;
    transition: all .3s ease-in-out;
}


.creative-animated .pt-plus-bg-image-animated {
    
    transform: translateX(0);
    transition-delay: 0s;
    transition: all 0s;
}

.pt-plus-bg-image-animated.top {
    transform: translateY(-100%);
    transition-delay: 0s;
}
.vc_single_image-wrapper.pt-plus-bg-image-animated {
    overflow: hidden;
    display: block;
}
.elementor *, .elementor :after, .elementor :before {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
.pt-plus-bg-image-animated {
    transition: all 0.3s;
    transition-duration: 0.5s;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    background-size: cover!important;
    background-position: center center!important;
}



.elementor-3240 .elementor-element.elementor-element-18cc200 .pt-plus-animated-image-wrapper .scroll-image-wrap .creative-scroll-image {
    min-height: 600px;
    transition: background-position 10s ease-in-out;
    -webkit-transition: background-position 10s ease-in-out;
}

.pt-plus-animated-image-wrapper .scroll-image-wrap .creative-scroll-image {
    display: block;
    position: relative;
    width: 100%;
    padding: 0;
    margin: 0;
    background-position: top;
    background-repeat: no-repeat;
    min-height: 400px;
    height: 100%;
    overflow: hidden;
    transition: background-position 2s cubic-bezier(.1,.54,.43,.94);
    background-size: cover;
}
.creative-animated .pt-plus-bg-image-animated.top:after {
    -webkit-transform: translateY(102%);
    -moz-transform: translateY(102%);
    -ms-transform: translateY(102%);
    -o-transform: translateY(102%);
    transform: translateY(102%);
}
.bg-image5f72f4368fbfa .pt-plus-bg-image-animated:after {
    background: rgba(0,0,0,0);
}
.creative-animated .pt-plus-bg-image-animated:after {
    -webkit-animation-fill-mode: both;
    -moz-animation-fill-mode: both;
    -o-animation-fill-mode: both;
    animation-fill-mode: both;
}
.pt-plus-bg-image-animated.top:after {
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
    transform: translateY(0);
}
.elementor *, .elementor :after, .elementor :before {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
.pt-plus-bg-image-animated:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #d3d3d3;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -ms-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    -webkit-transition-delay: 0.6s;
    -moz-transition-delay: 0.6s;
    -ms-transition-delay: 0.6s;
    -o-transition-delay: 0.6s;
    transition-delay: 0.6s;
}
