        width: 32px;
        height: 32px;
        opacity: 0.3;

            &:hover {
            opacity: 1;
            }


            &:before {
            position: absolute;
            left: 15px;
            content: ' ';
            height: 33px;
            width: 2px;
            background-color: $white;
            transform: rotate(45deg);
            }

            &:after {
            position: absolute;
            left: 15px;
            content: ' ';
            height: 33px;
            width: 2px;
            background-color: $white;
            transform: rotate(-45deg);
            }
        }
    } 

