      @keyframes glowing {
        0% { background-color:$h1red; box-shadow: 0 0 10px $h1red; }
        50% { background-color:  $buttonred; box-shadow: 0 0 30px $buttonred; }
        100% { background-color: $h1red; box-shadow: 0 0 10px $h1red; }
        }
    }
    a {
        font-size: 2rem;
        padding: 11px 15px;
        background: $h1red;
        color: $white;
        border: none;
        outline: none;
        animation: glowing 1300ms infinite;
        @keyframes glowing {
          0% { background-color:$h1red; box-shadow: 0 0 10px $h1red; }
          50% { background-color:  $buttonred; box-shadow: 0 0 30px $buttonred; }
          100% { background-color: $h1red; box-shadow: 0 0 10px $h1red; }
          }
      }
  }
  
  #popup {
