.footer {
    background:$woodsgreen;
    padding: 1.5em 0em 0em 0em;
    .flagContainer {
        padding-left: 0px;
        position: relative;
        left: -3px;
        color: $white;
        top:2.3em;
        @media (min-width:$screen-lg-min) { 
           top: 1.3em;
           left: 2em;
        }
        label {
            display: inline-block;
            vertical-align: middle;
        }
           
    
        .madeInUkraine{
            position: relative;
            display: inline-block;
            vertical-align: middle;
            width:100%;
            max-width:75px;
            height:32px;
            .topBlue{
                width:100%;
                height: 16px;
                position:inherit;
                top:0;
                left:0;
                background:$hoverlink;
            }
            .bottomYellow{
                width:100%;
                height: 16px;
                position:inherit;
                top:50;
                left:0;
                background:$yellow;
            }
            
        }
    }

    .container{
        width: 100%;
        max-width: 670px;
    }
    .footer-ns{
        display: none;

       
    }

    .footer-logo {
        padding-top: 40px;
        padding-top: 13px;

        p{
            text-align: center;
            @media (orientation: landscape)
            {
                text-align: left;     
                
            }
            @media (min-width:$screen-md-min) { 
                text-align: left;
            }
        }

        a {
            display: inline-block;
            text-align: center;           
            @media (orientation: landscape)
            {
                text-align: left;     
                
            }
            
            margin: 20px 0;
            width: 100%;
            
            @media (min-width:$screen-sm-min) { 
                text-align: left;
                margin: 0 0;
            }
            
            img {
                max-width: 70%;
            }
        } 
      
    }
    p { 
        font-size:12px; 
        color: $footerp;  
        margin-bottom: 8px;
    }
       

    .footer-brand {
        

        h2 { 
            margin:0px 0px 10px; 
        }
        
        p { 
            font-size:9px; 
            color: $footerp; 
            @media (min-width:$screen-md-min) { 
                font-size: 12px;
            }
        }
    }
    
    .footer-nav,
    .menuFooter {
        text-align: left;        
        color: $white;
        
        
        .list { 
            list-style:none; padding:0px; 
          
            li { 
                padding:5px 0px; 
                font-size: 18px;
            }
            a { 
                color:$white;
          
                &:hover { 
                    color: $footerp; 
                    text-decoration:none; 
                }
            }
        }    
    
        h4 {
            font-size: 13px;
            text-transform: uppercase;
            letter-spacing: 3px;
            margin-bottom:10px;  
        }
    }
    

    .footer-ns,
    .footer-social{
        padding-left: 0em!important;
        .pages { 
           
                li{
                    text-align: left;
                    a{
                        text-align: left;
                    }
                }
            }
            
        
        @media (min-width:$screen-sm-min) { 
            padding-left: 1.9em!important;
        }

        .col-xs-6
        {
            padding-left: 0em!important;

            @media (min-width:$screen-sm-min) { 
                padding-left: 1.9em!important;
            }
        }
    }
    .footer-nav {               
        width: 37%;
        @media (orientation: landscape)
        {
            width: 50%; 
        }
       
        @media (min-width:$screen-sm-min) { 
            width: 45%;
        }
        @media (min-width:$screen-md-min) { 
            width: 50%;
        }
    }

    .footer-social {       
        
        width: 63%;
        @media (orientation: landscape)
        {
            width: 50%;  
        }

        @media (min-width:$screen-sm-min) { 
            width: 55%;
        }
        @media (min-width:$screen-md-min) { 
            width: 50%;
        }
    }
   
    .footer-social,
    .menuFooter,
    .footer-nav {       
        color:$white; 
        .pages,
        ul { 
            list-style:none; 
            padding: 0px; 
            
            li { 
                            
                a { 
                    color:$white;
                    font-size: 16px;
                    font-weight: bold;

                    @media (min-width:$screen-sm-min) { 
                        font-size: 16px;
                    }
                    
                    &:hover {
                        color: $footerp; 
                        text-decoration:none;
                    }
                }  
            }
        }
        
        h4 {
            font-size: 13px;
            text-transform: uppercase;
            letter-spacing: 2px;
        }
    }  
    /*--footernew--*/

    .footer-ns {

        .menuFooter{ 
            color: $white;
            text-align: center;
            
            @media  (min-width:$screen-sm-min) {
                text-align: left;

                
            }
        }

        h4 {
            font-size: 13px;
            text-transform: uppercase;
            letter-spacing: 3px;
            margin-bottom:10px;
        }

        p { 
            font-size:12px; 
            color: $footerp;  
            margin-bottom: 8px;
            text-align:center;

            @media  (min-width:$screen-sm-min) {
                text-align: left;

                
            }
        }    
    }
       
    .footer-bs {
        .footer-ns{
           
            p { 
                font-size:12px; 
                color: $footerp;  
                margin-bottom: 8px;
            }  
        } 
    }

    @media (min-width:$screen-md-min) {               
                
        .footer-bs {
            background-color: $black;
            padding: 60px 40px;
            color:$white;
            
        }
    }   
    
    .col-md-5 {

        img {
            width: 100%;
            height: auto;    
            margin: auto;
        }
    }

  
   .form-container{
        position:relative;
        width: 100%;
        max-width: 296px;
        margin:auto;
        left:1.4em;
        @media (min-width: $screen-sm-min) {                
              left:0;  
        }
        .g-recaptcha {
            position:absolute;
            right: 13%;
            right: 16%;
            top:0;
            border-radius: 0px;

        }
        #form_id {
            width: 100%;
            max-width: 270px;
        
            .btn
            {
                border-radius: 0px 30px 30px 0px;
            }
            
            fieldset{
                display: inline-block;
                vertical-align: top;
                width: 77%;
            }
        
        }   
    }
   

}/*end of footer*/