.content-main {
    .timer {

        color:$white;
        width: 100%;
        max-width: 320px;
        margin: auto;
        text-align: center;
        float:none;
        padding-top: 0.8em;
        
         h3 {
            color:$white;
            text-align:center
            span {
                color:$timerred;
                color:$red;
            }
        }
        .salePrice {

            em{
                color:$timerred;
                color:$red!important;
            }
        }

         

        #content {
        display: none;
        padding: 1em;
        }

        #headline{
             color:$white;
             text-align: center;
             span {
            color:$timerred;
            }
        }

         

        
        li {
            display: inline-block;
            font-size: 1.5em;
            list-style-type: none;
            padding: 0.3em;
            text-transform: uppercase;
            text-align: center;
            width: 22%;
            span {
                display: block;
                font-size: 1.3em;
            }
            p {
                font-size:0.6em;
                color:$white;
            }
        }
        
        .emoji {
            padding: 0 .25em;
        }
        
    }

           
    .moblead {
        display: block;
        background: $black;
        padding: 1.5em 0em;
        @media (min-width: 1025px) {        
           
         
        }
        
        .mobleadblock {
            text-align: center;
            height: 100%;
            min-height: 120px;
            padding-top: .8em;
            margin-bottom: 1.4em;
            padding-left: 7px;
            padding-right: 7px;
            
            button {
                color:$white;
                padding: 10px 10px;
                margin: 5px;
                margin-top: 1.5em;
                animation: glowing1 1300ms infinite;
                @keyframes glowing1 {
                    0% { background-color:$hoverlink; box-shadow: 0 0 10px$hoverlink; }
                    50% { background-color:$hoverlinkb; box-shadow: 0 0 30px$hoverlinkb; }
                    100% { background-color:$hoverlink; box-shadow: 0 0 10px $hoverlink; }
                }
            }

            &:hover {
                background-color:$hoverlink;
               
                button {
                    animation: glowing2 1200ms infinite;
                    @keyframes glowing2 {
                        0% { background-color:$bybuttonred; box-shadow: 0 0 10px$bybuttonred; }
                        50% { background-color: $redshine; box-shadow: 0 0 30px $redshine; }
                        100% { background-color:$bybuttonred; box-shadow: 0 0 10px$bybuttonred; }
                    }
                  

                }
                
            }
            .title{
                font-size: 1.3em;
                color:$white;
                position: relative;
                margin-bottom: 0.5em;
                padding-bottom: 0.15em;
                width: 100%;
                max-width: 320px;
                margin: auto;
                &::before {
                    position: absolute;
                    content:"";
                    display: block;
                    height: 1px;
                    width: 100%;
                    background:$hoverlink;
                    left: 0;
                    bottom:0;
                } 
            }
            .price{
                font-size: 1em;
                font-weight: 400;
                color: $white;
                line-height: 1.2em;
                padding-top:1.2em;
                em {
                    color: $yellow;
                    font-style: normal; 
                }
                b {
                    text-decoration: line-through;
                    color: $black;
                    font-size: 1.1em;
                    font-weight: 500;
                }
           }
           .description{
                font-size: 1em;
                font-weight: 400;
                color:$white;
                line-height: 1.5em;
                width: 100%;
                max-width: 320px;
                margin: auto;
           }
          
        }
    }
    .widget-wrap {
        
          text-align: center;
          font-size: 1.5em;
          margin: 2em 0em;
          
          @media (min-width: $screen-sm-min) {        
            
        
          }
        
        
          @media (min-width: $screen-md-min) {            
          
          }
        
          @media (min-width: $screen-lg-min) {
            margin: 2em 0em;  
           
          } 
        
          h3{
            color: $fd;
            font-size:1.5rem;
            margin: 2em 0em; 
            @media (min-width: $screen-md-min) {
                margin: 2em 0em; 
                font-size: 3.9rem;   
            }  
            @media (min-width: $screen-lg-min) {
                margin: 2em 5em; 
                font-size: 2.5rem;   
            }              
        }
        h1 {
            color:$black;
            font-size: 2em;

        }

        .h1description{
            font-size: .5em;
            text-align: justify;
        }
    }
    .steps-text {
        padding: 10px 10px;
        text-align: justify;
        p{
            color:$fd;
            font-size: 1.1em;
            text-indent: 16px;

            b{
                font-weight: 700;
            }
        }
        @media (min-width: $screen-sm-min) {        
                padding: 10px 20px;
                
        }
        
            
        @media (min-width: $screen-md-min) {            
                padding: 10px 30px;
        }
        
        @media (min-width: $screen-lg-min) {
                padding: 10px 50px;
        }   
    }
    .step-end {
        text-align: center;
        font-size: 1.1rem;
        margin:0;
        margin-bottom: 2em;
        @media (min-width: $screen-lg-min) {
            font-size: 1.5rem;
        } 
    }

    .container_flex {
        
        margin: 0 auto;
            margin-top: 40px;
        color:$fd;
        margin-left: 15px;
        margin-right: 15px;
        @media (min-width: $screen-sm-min) {        
            
             
       }
       
        
       @media (min-width: $screen-md-min) {            
        
        margin-left: 0;
        margin-right: 0;
       }
       
       @media (min-width: $screen-lg-min) {
        
        } 
        
        .steps-box{
            display: flex;
            justify-content: center;
            margin-bottom: 4rem;
            
            .steps-item__line {
               
                background-color: $steps;
                position: relative;
                top: 3.4rem;
                  margin-right: 0.93rem;
                     margin-left: 0.93rem; 
                
                @media (min-width: $screen-sm-min) {        
                    width: 0px;
                    height: 0px;
                    margin-right: 1.5rem;
                    margin-left: 1.5rem;
                      
                }
                
                 
                @media (min-width: $screen-md-min) {            
                    width: 3.62rem;
                     height: 2px;
                    margin-right: 2.93rem;
                     margin-left: 2.93rem;
                }
                
                @media (min-width: $screen-lg-min) {
                     width: 5.62rem;
                     height: 2px;   
                     margin-right: 2.93rem;
                     margin-left: 2.93rem;
                 } 
            }
            .steps-item__number {
                font-size: 2rem;
                font-weight: 700;
                    margin: 5px 5px;
                @media (min-width: $screen-sm-min) {        
                    font-size: 3rem;
                    
       
                }
                
                
                @media (min-width: $screen-md-min) {            
                    font-size: 5rem;
                   
                    
                }
                
                @media (min-width: $screen-lg-min) {
                    font-size: 6rem;
                   

                 }
            }
            .steps-item__text {
                width: 28rem;
                
                h6 {
                    font-size: 2rem;
                    margin: 0;
                    font-weight: 700;
                    text-transform: inherit;
                }
                h3 {
                    font-size: 2rem;
                    margin: 0;
                    font-weight: 700;
                    text-transform: inherit;
                }
                h5 {
                    color:$hoverlink;
                    font-weight: 700;
                }
                p {
                   text-align: justify;
                    margin: 0;
                    padding: 5px 5px;
                    text-transform: inherit;
                    font-size: 1.1em;
                    text-indent: 16px;
                
               
                
                   
                }
            }
        }

        
       
        .s-item_1 {
           
            .steps-item__number {
                color: $steps;
            }
        }
         
      
        
     
        
        
        .s-item_2 {

            .steps-item__text {
                text-align: right;
                
            }   
           
            .steps-item__number {
            color: $steps;
            }
        }

      
         
        
    
         
    }

    .whyUs {   
        .col-xs-12 {
            min-height: 279px;
            @media (min-width: $screen-sm-min) {        
                min-height: 358px;
            
            }
        } 
        .row {
            padding: 0!important;
            margin: 0!important;
           .col-sm-6 {
                padding: 0!important;
                margin: 0!important;
                width: 100%!important; 
                float: none!important;
                display: inline-block;
                vertical-align: top;
                @media (min-width: $screen-sm-min) {        
                    width: 49%!important; 
                
                }
                
                
                @media (min-width: $screen-md-min) {            
                
                }
                
                @media (min-width: $screen-lg-min) {
                    
                } 
            }
        }

        .whyushead {
            text-align: center;
            font-size: 2em;

           

            .widget-wrap {

                h4{
                    font-size:0.8em!important
                }
            }
            
            
        }
        

               
            
        
        /*whyus section*/
    
        h2{
            color: $black;
            font-size:1.5rem;
            margin: 2em 0em; 
            @media (min-width: $screen-md-min) {
                margin: 2em 0em; 
                font-size: 3.9rem;   
            }  
            @media (min-width: $screen-lg-min) {
                margin: 2em 3em; 
                font-size: 2.5rem;   
            }              
        }
    
        p{
            color: $black;
            margin:   1em 1em;
            @media (min-width: $screen-sm-min) {        
                margin:   1em 3em; 
            
            }
        }
    
        
    
        
        
        
    
    
        .why-steps__box {
            margin-top: 70px;
        }
        .why-us-heading {
            font-size: 2em;
            font-weight: 700;
            font-family: 'Proxima Nova',sans-serif;
            letter-spacing: 0.6px;
            margin: 1em 0em;
        }
        .why-us-text {
            font-size: 1.1em;
            text-align: justify;
            line-height: 1.15em;
            text-indent: 2.5em;
            color: $black;
            
                b{
                    font-weight: 700;
                }
            
        }
        
    
        .image-container .after {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: none;
            color:$white;
        }
        .image-container:hover .after {
            display: block;
            background: $black;
            opacity: 0.6;
        }
    }

    .ourclients{
        .col-xs-12 {
            margin-bottom: 1.2em;
            
            @media (min-width: $screen-sm-min) {        
                margin-bottom: 0; 
                min-height: 731px;
            }
            @media (min-width: $screen-md-min) {        
                 
            
            }

        }
        .whyushead {
            min-height: unset;
        }
         .row {
             margin: 0;
         }
        .ourclientssay{
            font-size:1.2em!important;
            padding:35px 15px;
        } 

        .ourclients-heading b
        { 
            font-size:1.4em;
            
        }

        .ourclients-heading{
            font-size:1.2em;
            padding:10px 0px;
            min-height: 120px;
        }

        .ourclients-heading{
             .seo-img{
            border-radius:50%;
             }
        }
        

        .widget-wrap {
            margin: 0 0;
            h1 {
                color:$h1red;
                color:$red;
                font-size: 1.6rem;
            } 
            p{
                font-size: 1.4rem;
            }
            h4 {
                font-size: 1.4rem;
                padding:0px 15px;
            }
            h2{
                padding:15px 0px;
            }
            span{
                color:$black;
            }
        }

        

        

        

       

        .mobleadblock:hover,
        .mobleadblock:hover button 
        {
            color: $black!important;
            text-decoration: underline;
                
        }

        .ourclients-text {
            width:100%;
            text-align:justify;
            max-width:270px;
            margin:auto;
            font-style:italic;
            text-indent:16px;
            font-size:1.1em;
            padding:0px 15px;
            b{
                font-weight: 700;
            }
        }

        .step-end h6{
            padding:15px 0px;
            margin: 0;
        }

        .content-main .whyUs .why-us-heading {            
            margin: 0em 0em 1em 0em;
        }
    }
    .centr {
        text-align: center;
    }
    .blackline {
        background: $woodsgreen;
        text-align: center;
        min-height: unset;
        h3 {
            margin-top: 2em;
            margin-bottom: 2em;
            font-size: 2em;
            color:$white;
            text-transform: uppercase;
            font-weight: 600;
        }
    }
    .products{

       
        padding-top: 30px;
        padding-bottom: 30px;
        .product {
            text-align: center;
            .productname{
                font-size: 1.5em;
                margin-bottom: 10px;
            }
            .product-price{
                p{
                font-size: 1.5em;
                margin-top: 20px;
                text-align: left;
                font-weight: 500;
                }
            }
        }
    } 
    
}