.services{
    .darkin{
        color:$fd;
        table{
            tbody{
                tr{
                    td,th {
                        border-top: 1px solid $hoverlink;
                        a {
                            color:$prjctyellow;
                            border: none;
                        }
                    }
                }
            }
        }
    }
    h1{
        padding: 25px 0px;
    }
    .glow {
        color:$black!important;
        background-color:$yellow;
        animation: glowing1 1.3s infinite;
        @keyframes glowing1 {
            0% { background-color:$yellow; box-shadow: 0 0 10px $yellow; }
            50% { background-color:  $servicesyellow; box-shadow: 0 0 30px $servicesyellow; }
            100% { background-color: $yellow; box-shadow: 0 0 10px $yellow; }
            } 
    }
    .widget-wrap{
        h1{ 
            padding: 25px 0px;
        }
    }
    .widget-wrap {
        margin-bottom: 30px;
        color: $fd;
    }

    
   
   
    .services-widget1 {
        position: relative;    
    
        background-color: $nav;
        


        .serviceslist {
            color: $white;
            padding-top: 20px;
            font-size: 14px;
            ul
            {
                li {
                    list-style: none;            
                    
                    text-align: left;
                    padding: 5px 8px;
                    

                    a {
                        
                        
                        h3,h4,h6{
                            color: $white; 
                            font-size: 16px;
                        }

                        &:hover{
                            text-decoration: none;
                          
                            h3,h4,h6{   
                                background: $yellow;                             
                                color: $black;
                                padding-left: 1.5%;
                            }
                        }
                    }

                    @media (min-width: $screen-md-min) {  
                        padding: 5px 15px;

                        a {
                            
                                        
                            h3,h4,h6{
                                color: $white; 
                                font-size: 18px;
                            
                            }
                        }
                    }
            
                    @media (min-width: $screen-lg-min) {  
                        a {
                            font-size: 1.1em;
                            
                        }
                    }
                }
            } 
            
        }

        .service-element1
        {
            position: relative;
            min-height: 317px;
            padding-top: 50px;
            padding-bottom: 50px;

            @media (min-width: 490px) {  
                min-height: 317px;
            }
            @media (min-width: 590px) {  
                
            }
            @media (min-width: $screen-sm-min) {  
                
                min-height: 468px;
            }

            @media (min-width: $screen-md-min) {  
                min-height: 440px;
                
            }

            .service-plate {
            
                display: none;
            }
        }
        



        .servicestext {
            text-align: center;
            color: $white;
            margin: 0 auto;
            position: relative;
            left: 0;
            right: 0;
            z-index: 999;

            h2{
                font-size: 1.8em;
                padding: 5px 8px;
                color: #fff;
                text-align: left;
                color: $white;
                @media (min-width: $screen-sm-min) {  
                    font-size: 1.1em;
                }
                @media (min-width: $screen-md-min) {   
                    font-size: 1.0em;
                    padding: 0 .5em;
                    padding: 0em 0.5em;
                    font-size: 1.3em;
                    text-align: center;
                }

                @media (min-width: $screen-lg-min) {  
                    font-size: 1.5em;
                }
            }
        }
    }

    .services {
        border-bottom: 1px solid ;
        
        padding:10px 10px 10px 10px;
        .service {
            color:$hoverlink;
            
            margin-bottom: 20px;
            font-weight: 500;
            @media (min-width: $screen-sm-min) {        
                        font-size: 1em;     
                    }
                    @media (min-width: $screen-md-min) {            
                    font-size: 2em;
                    }
                    @media (min-width: $screen-lg-min) {
                        font-size: 2em;
                    } 
        }
        .serv-text {
            margin-bottom: 10px;
            color:$fd;
            a {
                color:$prjctyellow;
            }
        }
        .leads {
            text-align: center;
            .leadbut{
                font-size: 1.2em;
                padding: 11px 15px;
                color:$hoverlink;
                border: none;
                outline: 0;
                background: none;
                text-transform: uppercase;
                font-weight: 500;
                
                .lead {
                    font-family: 'Proxima Nova',sans-serif;
                    position: absolute;
                    transform: translate(-50%,-50%);
                    left: 50%;
                    top:5em;
                    background: none;
                    font-weight: 400;
                    font-size: 1.2em;
                    @media (min-width: $screen-md-min) {            
                        top:3.5em;
                    }
                    
                    &:before {
                        top: -0.1rem; 
                        bottom: 100%; 
                        left: 50%; 
                        right: 50%;
                        transition: bottom .15s 0s, left .4s .3s, right .4s .3s;
                            display: block;
                            content:"";
                            position: absolute;
                        background: $yellow;
                        z-index: -1;
        
                        }
                    &:after {
                        top: 100%; 
                        bottom: -0.1rem; 
                        left: 50%; 
                        right: 50%;
                        transition: top .15s 0s, left .4s .3s, right .4s .3s;
                        display: block;
                            content:"";
                            position: absolute;
                        background:$yellow;
                        z-index: -1;
                    }
                    &:hover {
                        color:$black;
                        transition: .15s;
                        &:before {
                            top: -0.1rem; 
                            bottom: 50%; 
                            left: -0.1rem; 
                            right: -0.1rem;
                            transition: left .15s 0s, right .4s 0s, bottom .3s .15s;
                        }
                        &:after {
                            top: 50%; 
                            bottom: -0.1rem; 
                            left: -0.1rem; 
                            right: -0.1rem;
                            transition: left .15s 0s, right .4s 0s, top .3s .15s;
                        }
                    }             
                }
                } 
        }
    }
    
}

